/*
*two-column
*/

//two-column-layout1

.two-column-layout1 {
    margin-bottom: 81px;

    .figure1 {
        width: 50vw;

        img {
            box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
            border-bottom-left-radius: 60px;

        }
    }

    .description {
        padding: 37px 0;

        p {
            line-height: calc(28 / 15);
            margin-bottom: 34px;
        }

        h2 {
            margin-bottom: 28px;
        }
    }
}

@media(min-width: 1400px) {
    .two-column-layout1 {
        .figure1 {
            img {
                max-height: 560px;
            }
        }
    }
}

@media(max-width: 767px) {
    .two-column-layout1 {
        .figure1 {
            img {
                max-height: 290px;
            }
        }
    }
}

@media (max-width: 991px) {
    .two-column-layout1 {
        .description {
            padding: 37px 0;

            p {
                line-height: calc(22 / 12);
                font-size: rem-calc(12);
            }

            h2 {
                margin-bottom: 20px;

                span {
                    margin-bottom: 14px;
                }
            }
        }
    }
}

@media (max-width: 767px) {
    .two-column-layout1 {
        margin-bottom: 32px;

        .figure1 {
            width: 100vw;
        }
    }
}

//two-column-layout2

.two-column-layout2 {
    .container {
        padding-bottom: 85px;
        border-bottom: 2px solid $white;
        z-index: 2;
        position: relative;
    }

    &.bg-pattern1 {
        overflow: hidden;
        &::after {
            top: auto;
            bottom: -384px;
            left: 100px;
            width: 933px;
            height: 805px;
            transform: rotate(33deg);
        }
    }

    .figure1 {
        width: 50vw;

        img {
            border-bottom-right-radius: 60px;
        }
    }

    h2 {
        color: $white;
        margin-bottom: 32px;
    }
}

@media(max-width: 767px) {
    .two-column-layout2 {
        .container {
            padding-bottom: 0;
            border: none;
        }

        &.bg-pattern1{
            &::after{
                top: auto;
                bottom: 25px;
                right: -110px;
                left: auto;
                width: 532px;
                height: 323px;
            }
        }

        h2 {
            margin-bottom: 17px;
        }

        .figure1 {
            width: 100vw;

            img {
                max-height: 300px;
            }
        }
    }

}

@media(min-width: 1400px) {
    .two-column-layout2 {
        .figure1 {
            img {
                max-height: 586px;
            }
        }
    }
}