/*
*button
*/

.btn{
    padding: 8px 25px;
    font-size: rem-calc(14);
    font-weight: 600;
    border-radius: 20px;
    border-width: 2px;
}

.btn-outline-secondary{
    &:hover{
        color: $white !important;
    }
}

.btn-secondary{
    color: $white !important;
    &:hover{
        color: $white;
    }

}

@media (max-width: 767px){
    .btn{
        padding: 8px 20px;
    }
}