/*
*list
*/

//list1
.list1 {
    list-style: none;
    display: flex;
    justify-content: space-between;

    li {
        display: flex;
        align-items: center;
        .icon {
            width: 60px;
            height: 60px;
            background-color: $primary;
            border-radius: 50%;
            margin-right: 20px;
            z-index: -1;
            flex-shrink: 0;
            padding: 14px;
            img{
                width: 32px;
                height: 32px;;
            }
        }

        span {
            font-size: rem-calc(18);
            font-weight: 600;
            color: $secondary;
        }
    }
}

@media (max-width: 991px){
    .list1{
        li{
            width: 33%;
            margin: 0 5px;
            .icon{
                margin-right: 8px;
            }
        }
    }
}


@media (max-width: 767px){
    .list1{
        display: block;
        li{
            margin-bottom: 23px;
            width: 100%;
            font-size: rem-calc(16);
            &:last-child{
                margin-bottom: 0;
            }
            .icon{
                margin-right: 20px;
            }
        }
    }
}


//list2

.list2{
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    li{
        display: flex;
        align-items: center;
        width: 40%;
        margin-bottom: 75px;
        .icon{
            margin-right: 21px;
            img{
                min-width: 72px;
            }
        }
        .content-wrapper{
            h3{
                color: $white;
                font-size: rem-calc(21);
                margin-bottom: 9px;
            }
            p{
                font-size: rem-calc(14);
                letter-spacing: 0.28px;
                line-height: calc(20 / 14);
                color: $white;
            }
        }
    }
}

@media (max-width: 991px){
    .list2{
        li{
            width: 45%;
            display: block;
            margin-bottom: 40px;
            .icon{
                display: block;
                margin-bottom: 10px;
            }
        }
    }
}

@media (max-width: 767px){
    .list2{
        display: block;
        li{
            width: 100%;
            display: flex;
            .content-wrapper{
                h3{
                    font-size: rem-calc(16);
                }
                p{
                    font-size: rem-calc(12);
                    color: $white;
                }
            }
            .icon{
                img{
                    max-width: 60px;
                    min-width: auto;
                }
            }
        }
    }
}