h2 {
    span {
        display: block;
        font-size: rem-calc(18);
        letter-spacing: 0.18px;
        margin-bottom: 15px;
        line-height: calc(28 / 18);
        color: $secondary;
    }
}

@media(max-width: 767px) {
    h2 {
        span {
            margin-bottom: 10px;
            font-size: rem-calc(14);
            letter-spacing: 0.14px;
        }
    }
}

.figure1 {
    img {
        object-fit: cover;
    }
}

ul {
    padding: 0;
    margin: 0;
}

.icon {
    display: inline-block;
    vertical-align: middle;
}

.pulled-down {
    margin-bottom: -119px;
}

@media(max-width: 991px) {
    .pulled-down {
        margin-bottom: -150px;
    }
}

@media(max-width: 767px) {
    .pulled-down {
        margin-bottom: -125px;
    }
}

.pt-extra {
    padding-top: 200px !important;
}

.bg-color1 {
    background-color: #EEF2FC;
}

.call-us {
    h6 {
        font-size: rem-calc(40);
        color: $white;
        margin-bottom: 27px;
    }

    .icon {
        margin-right: 15px;
    }

    span {
        font-size: rem-calc(21);
        color: $white;
        font-weight: 500;

        a {
            text-decoration: none;
            color: $pink;
            font-weight: 700;
            letter-spacing: 0.21px;
        }
    }
}

@media (max-width: 767px) {
    .call-us {
        h6 {
            font-size: rem-calc(27);
        }

        span {
            font-size: rem-calc(16);
        }
    }
}

.d-ltr {
    direction: ltr;
}

.d-rtl {
    direction: rtl;
}

.bg-pattern1 {
    position: relative;

    &::after {
        content: " ";
        z-index: 0;
        background-image: url(../images/bg-pattern1.svg);
        background-repeat: no-repeat;
        position: absolute;
        top: 0;
        width: 933px;
        height: 571px;
        opacity: 0.03;
    }
}

@media (max-width: 767px) {
    .bg-pattern1 {
        &::after {
            background-size: cover;
            width: 406px;
            height: 249px;
        }
    }
}

.border-bottom1 {
    border-bottom: 2px solid $white;
}

@media (max-width: 576px) {
    .container {
        padding-left: 25px;
        padding-right: 25px;
    }
}

#scroll-down {
    position: absolute;
    bottom: 66px;
    left: 43%;
    background: none;
    border: none;
    display: inline-block;
    width: auto;
    z-index: 110;

    .icon {
        img {
            max-width: 37px;
        }
    }
}

@media (max-width: 1199px) {
    #scroll-down {
        left: 45%;
    }
}

@media (max-width: 767px) {
    #scroll-down {
        left: auto;
        bottom: auto;
        right: 37px;
        top: 33%;

        .icon {
            img {
                max-width: 25px;
            }
        }
    }
}

@media (max-width: 576px) {
    #scroll-down {
        left: auto;
        right: 20px;
        top: 28%;
    }
}