/*
*section
*/

.section {
    padding: 80px 0;
}

@media (max-width: 767px) {
    .section {
        padding: 45px 0;
    }
}

/*
*section1
*/

.section1 {
    h2 {
        margin-bottom: 25px;

        span {
            margin-bottom: 27px;
        }
    }

    p{
        line-height: calc(20 / 15);
        margin-bottom: 29px;
    }

    .figure1 {
        margin-top: -40%;

        img {
            border-bottom-right-radius: 60px;
            border-top-left-radius: 60px;
            border-top-right-radius: 60px;
        }
    }
}

@media(max-width: 991px) {
    .section1 {
        .figure1 {
            margin-top: -50%;
        }
    }
}

@media(max-width: 767px) {
    .section1 {
        .figure1 {
            margin-top: 0;
            margin-bottom: 30px;
        }
        h2{
            span{
                margin-bottom: 15px;
            }
        }
    }
}