/*
*box
*/

//box1
.box1 {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    border-radius: 30px;
    padding: 50px 40px 36px 40px;
    text-align: center;
    background-color: $white;
    z-index: 100;
    position: relative;

    h2 {
        margin-bottom: 20px;
    }

    p {
        color: $secondary;
        margin-bottom: 20px;
    }
}

@media (max-width: 767px) {
    .box1 {
        padding: 30px;

        h2,
        p {
            margin-bottom: 15px;
        }
    }
}