/*
*icon-text
*/

.icon-text{
    .icon{
        img{
            margin-bottom: 27px;
            width: 60px;
            height: 60px;
        }
    }
    h5{
        margin-bottom: 16px;
        font-size: rem-calc(24);
    }
    p{
        font-size: rem-calc(14);
        line-height: calc(23 / 14);
        letter-spacing: 0.28px;
    }
}

@media(max-width: 767px){
    .icon-text{
        .icon{
            img{
                margin-bottom: 20px;
            }
        }
        h2{
            font-size: rem-calc(20);
        }
        p{
            font-size: rem-calc(12);
        }
    }
}