/*
*container
*/

#container {
    min-width: 320px;
    min-height: 100vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;

    #content {
        flex: 1;
    }
}

/*
*logo
*/
.logo {
    img {
        max-width: 263px;
    }
}



/*
*header
*/

#header {
    padding: 45px 0;
    position: fixed;
    width: 100%;
    z-index: 200;
    @include cubic-transition;
}

@media(max-width: 991px) {
    #header {
        padding: 27px 0;

        .logo {
            z-index: 300;
            position: relative;

            img {
                max-width: 161px;
            }
        }
    }

}


