/*
*menu-btn
*/
.menu-btn {
    border: none;
    background: none;
    display: block;
    margin-left: auto;
    padding: 0;

    span {
        display: block;
        width: 20px;
        height: 2px;
        background-color: $white;
        margin-bottom: 7px;
        @include cubic-transition;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &.active {
        z-index: 300;
        position: relative;

        span {
            &:first-child {
                transform: rotate(45deg) translate(6px, 6px);
            }

            &:last-child {
                transform: rotate(-45deg) translate(6.5px, -6px);
            }

            &:nth-child(2) {
                opacity: 0;
            }
        }
    }
}


/*
*footer
*/
#footer {
    background-color: $primary;
    padding-top: 66px;
    padding-bottom: 19px;
    overflow: hidden;
    position: relative;
    z-index: 1;

    &::after {
        content: " ";
        z-index: -1;
        background-image: url(../images/bg-pattern1.svg);
        background-repeat: no-repeat;
        position: absolute;
        width: 933px;
        height: 805px;
        opacity: 0.03;
        left: 100px;
        top: -424px;
        transform: rotate(33deg);
    }

    p {
        font-size: rem-calc(13);
        letter-spacing: -0.13px;
        font-weight: 600;
        color: $white;
    }

    .navigation {
        list-style: none;
        display: flex;
        justify-content: flex-end;

        li {
            margin: 0 55px;

            &:last-child {
                margin-right: 0;
            }

            &:first-child {
                margin-left: 0;
            }

            a {
                font-size: rem-calc(14);
                letter-spacing: -0.14px;
                font-weight: 600;
                color: $white;
                text-decoration: none;
            }
        }
    }

    .credit {
        a {
            color: $pink;
        }
    }
}

@media(max-width: 1399px) {
    #footer {
        .navigation {
            li {
                margin: 0 45px;
            }
        }
    }
}

@media(max-width: 1199px) {
    #footer {
        .navigation {
            li {
                margin: 0 25px;
            }
        }
    }
}

@media(max-width: 767px) {
    #footer {
        padding: 48px 0;

        &::after {
            top: -60%;
            right: -110px;
            left: auto;
            width: 532px;
            height: 323px;
            background-size: cover;
        }

        .navigation {
            display: block;

            li {
                margin: 0 0 18px 0;
            }
        }
    }
}

.home {
    #main-navigation {
        .menu {
            li {
                a {
                    color: $primary;
                }
            }
        }
    }
}

/*
*banner
*/

#banner {
    position: relative;
    background-color: $gray-400;

    .carousel1 {
        .item {
            .figure1 {
                img {
                    height: 768px;
                    z-index: -99;
                    object-fit: cover;
                }
            }

            [class*='col-'] {
                padding: 0;
            }

            .description {
                position: relative;
                height: 100%;
                display: flex;
                flex-direction: column;

                &.bg-pattern1 {
                    &::after {
                        z-index: 2;
                        top: -110px;
                        right: -55px;
                    }
                }


                &::before {
                    content: " ";
                    background-color: $primary;
                    border-bottom-right-radius: 60px;
                    z-index: 1;
                    height: 100%;
                    width: 125%;
                    position: absolute;
                    top: 0;
                    right: -100px;
                }

                .inner-wrapper {
                    display: block;
                    max-width: 550px;
                    margin: auto auto 0 auto;
                    z-index: 100;
                    margin-bottom: 66px;
                    padding: 0 50px;

                    h1, .h1 {
                        color: $white;
                        margin-bottom: 35px;
                    }

                    p {
                        color: $white;
                        font-size: rem-calc(18);
                        letter-spacing: 0.72px;
                        margin-bottom: 0;
                    }
                }

            }
        }
    }
}


@media (max-width: 1199px) {
    #banner {
        .carousel1 {
            .item {
                .figure1 {
                    img {
                        height: 550px;
                    }
                }
            }
        }
    }
}

@media (max-width: 767px) {
    #banner {
        .carousel1 {
            .item {
                .description {
                    height: calc(100% + 110px);

                    &::before {
                        right: 15px;
                        width: 100%;
                    }

                    &.bg-pattern1 {
                        &::after {
                            top: 15px;
                            right: 60px;
                            width: 406px;
                            height: 249px;
                        }
                    }

                    .inner-wrapper {
                        max-width: 540px;
                        padding-left: 8px;
                        padding-right: 80px;
                        margin-bottom: 44px;

                        p {
                            font-size: rem-calc(12);
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 576px) {
    #banner {
        .carousel1 {
            .item {
                .description {
                    height: calc(100% + 90px);

                    .inner-wrapper {
                        padding-left: 30px;
                        padding-right: 71px;
                    }
                }
            }
        }
    }
}