.page-head {
    position: relative;
    min-height: 400px;
    padding-top: 90px;
    display: flex;
    align-items: center;
    overflow: hidden;

    &::before {
        content: " ";
        position: absolute;
        top: -45px;
        bottom: 0;
        width: 85%;
        background-color: $primary;
        border-bottom-right-radius: 60px;
        z-index: -1;
    }

    &.bg-pattern1 {
        &::after {
            z-index: -1;
            top: -135px;
            left: -307px;
        }
    }

    .description {
        max-width: 560px;
        position: relative;

        h1 {
            color: $white;
            font-size: rem-calc(48);
        }
    }
}

@media(max-width: 1199px) {
    .page-head {
        &::before {
            width: 86%;
        }
        .description {
            max-width: 480px;
        }
    }
}

@media(max-width: 991px) {
    .page-head {
        &::before {
            width: 100%;
        }
    }
}


@media(max-width: 767px) {
    .page-head {
        min-height: 325px;
        padding-top: 30px;

        &.bg-pattern1 {
            &::after {
                width: 500px;
                height: 305px;
                top: 15px;
                left: 0;
            }

        }

        .description {
            h1 {
                color: $white;
                font-size: rem-calc(35);
            }
        }
    }
}