/*
*banner
*/

//carousel1
.carousel1 {
    position: relative;

    .item {
        overflow: hidden;
    }

    .owl-dots {
        position: absolute;
        bottom: 66px;
        right: 56px;

        .owl-dot {
            width: 36px;
            height: 6px;
            background-color: $white;
            border-radius: 3px;
            margin: 0 3px;

            &.active {
                background-color: $secondary;
            }
        }
    }
}



@media (max-width: 1199px) {
    .carousel1 {
        .owl-dots {
            position: absolute;
            bottom: 66px;
            right: 30px;
        }
    }
}

@media (max-width: 767px) {
    .carousel1 {
        .owl-dots {
            bottom: 23px;
            left: 25px;
            right: auto;

            .owl-dot {
                width: 27px;
                height: 5px;

            }
        }
    }
}


//carousel2
.carousel2 {
    position: relative;
    text-align: center;
    padding: 0 35px;

    .item {
        .icon {
            img {
                margin-bottom: 35px;
            }
        }

        blockquote {
            margin-bottom: 40px;
            p {
                font-size: rem-calc(20);
                font-weight: 500;
                font-style: italic;
                color: $primary;
                line-height: calc(30 / 20);
                margin-bottom: 35px;
            }
            cite{
                font-size: rem-calc(18);
                font-weight: 600;
                color: $secondary;
            }
        }

    }
    .owl-dots {
        position: absolute;
        left: 50%;
        bottom: -15px;
        transform: translateX(-50%);

        .owl-dot {
            width: 21px;
            height: 5px;
            background-color: $white;
            border-radius: 3px;
            margin: 0 3px;
            background-color: $primary;
            &:first-child{
                margin-left: 0;
            }

            &.active {
                background-color: $secondary;
            }
        }
       
    }
    .owl-nav{
        .owl-prev{
            position: absolute;
            top: 50%;
            left: 0;
            svg{
                width: 35px;
            }
        }
        .owl-next{
            position: absolute;
            top: 50%;
            right: 0;
            svg{
                width: 35px;
            }
        }
        
    }
}

@media (max-width: 991px){
    .carousel2{
        .owl-nav{
            .owl-prev, .owl-next{
                top: 35%;
            }
        }
    }
}

@media (max-width: 767px){
    .carousel2{
        .item{
            blockquote{
                margin-bottom: 28px;
                p{
                    font-size: rem-calc(14);
                    line-height: calc(22 / 14);
                }
                cite{
                    font-size: rem-calc(14);
                }
            }
        }
        .owl-dots{
            bottom: -10px;
        }
        .owl-nav{
            .owl-prev{
                left: -35px;
            }
            .owl-next{
                right: -35px;
            }
        }
    }
}

@media(max-width: 576px){
    .carousel2{
        .item{
            max-width: 250px;
            margin-left: auto;
            margin-right: auto;
        }
        .owl-nav{
            .owl-prev{
                left: -25px;
            }
            .owl-next{
                right: -25px;
            }
        }
    }
}