/*
*post
*/

.post{
    h1,h2,h3,h4,h5,h6{
        letter-spacing: 0.18px;
        color: $secondary;
        margin-bottom: 40px;
    }
    h4{
        font-size: rem-calc(18);
        line-height: calc(28 / 18);
    }
    p{
        line-height: calc(28 / 15);
        margin-bottom: 30px;
    }

    ul,ol{
        padding-left: 16px;
        margin-bottom: 20px;
        li{
            margin-bottom: 10px;
            ul,ol{
                padding-top: 10px;
            }
        }
    }
}

@media (max-width: 767px){
    .post{
        h1,h2,h3,h4,h5,h6{
            margin-bottom: 20px;
        }
        p{
            margin-bottom: 15px;
        }
    }
}