/*
* form
*/

.form1 {
    form {
        &.invalid{
            .wpcf7-response-output {
                color: $red;;
            }
        }

        &.sent{
            .wpcf7-response-output {
                color: $green;
            }
        }
        
        .wpcf7-response-output {
            border: none;
            font-size: rem-calc(16);
            font-weight: 500;
            margin: 5px 0;
        }

        label {
            color: $white;
            font-size: rem-calc(14);
            font-weight: 500;
            line-height: calc(21 / 14);
        }

        .form-control {
            padding: 13px;
            border-radius: 3px;
            color: $gray-500;
            font-size: rem-calc(14);

            &.wpcf7-not-valid {
                border: 2px solid $red;
            }
        }

        textarea {
            &.form-control {
                min-height: 130px;
                margin-bottom: 35px;
            }
        }

        .btn {
            padding: 12px 25px;
            border-radius: 25px;
            position: relative;
            z-index: 2;
        }

        .check1 {
            position: relative;
            display: flex;
            align-items: center;
            margin-bottom: 35px;

            input {
                opacity: 0;
                position: absolute;
                top: 0;
                z-index: 1;

                &:checked {
                    ~label {
                        &::after {
                            display: block;
                        }
                    }
                }
            }

            label {
                position: relative;
                padding-left: 50px;
                font-weight: 400;

                &::before {
                    content: " ";
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 37px;
                    width: 37px;
                    border-radius: 50%;
                    background-color: $white;
                }

                &::after {
                    content: '';
                    width: 10px;
                    height: 24px;
                    border-style: solid;
                    border-color: transparent #000 #000 transparent;
                    border-width: 0px 2px 2px 0px;
                    position: absolute;
                    left: 14px;
                    top: 4px;
                    transform: rotate(45deg);
                    display: none;
                }
            }
        }
    }
}

@media (max-width: 767px) {
    .form1 {
        form {
            .form-conrol {
                label {
                    font-size: rem-calc(12);
                    line-height: calc(21 / 12);
                }
            }

            textarea,
            .check1 {
                margin-bottom: 25px;
            }
        }
    }
}